<template>
	<div id="single-blog">
		<h1>{{blog.title}}</h1>
		<p>作者：{{blog.author}}</p>
		<hr>
		<article>{{blog.content}}</article>
		<p>分类：</p>
		<ul>
			<li v-for="category in blog.categories" :key="category">
				{{category}}
			</li>
		</ul>
		<button v-on:click="deleteSingleBlog()">删除</button>
		<router-link :to="'/edit/' + id">编辑</router-link>
	</div>
</template>

<script>
	import axios from 'axios'
	export default{
		name:"single-blog",
		data(){
			return{
				id:this.$route.params.id,
				blog:{}
			}
		},
		created(){
			// this.$http.get('https://myblog-fcfec.firebaseio.com/posts/' + this.id + ".json")
			axios.get('/posts/' + this.id + ".json")
			.then((data) => {
				// this.blog = data.body;
				console.log(data);
				this.blog = data.data;
			})
		},
		methods:{
			deleteSingleBlog(){
				axios.delete('/posts/' + this.id + ".json")
				.then(response =>{
					this.$router.push({path:'/'})
				})
			},
		}

	}
</script>

<style scoped>
	#single-blog{
		max-width: 960px;
		margin: 0 auto;
		padding: 20px;
		background:#eee;
		border: 1px dotted #aaa;
	}

	button, a{
	/* display: block; */
	margin: 20px 0;
	background: crimson;
	color: #fff;
	border: 0;
	padding: 14px;
	border-radius: 4px;
	font-size: 18px;
	cursor: pointer;
	text-decoration: none;
	}

	article{
		white-space: pre-wrap;
	}
</style>


