<template>
  <div id="add-blog">
    <h2>写博客</h2>
    <form v-if="!submmited">
      <label>主题：</label>
      <input type="text" v-model="blog.title" required />

      <label>内容：</label>
      <textarea v-model="blog.content"></textarea>

      <label>分类：</label>
      <div id="checkboxes">
        <input type="checkbox" value="vue.js" v-model="blog.categories">
        <label>vue.js</label>
        <input type="checkbox" value="Angular4" v-model="blog.categories">
        <label>Angular4</label>
        <input type="checkbox" value="Node.js" v-model="blog.categories">
        <label>Node.js</label>
        <input type="checkbox" value="React.js" v-model="blog.categories">
        <label>React.js</label>
      </div>

      <label>作者：</label>
      <select v-model="blog.author">
        <option v-for="author in authors" :key="author">
          {{author}}
        </option>
      </select>
      <button v-on:click.prevent="post">添加博客</button>
    </form>

    <div v-if="submmited">
      <h3>您的博客发送成功！</h3>
    </div>

    <div id="preview">
      <h3>博客总览</h3>
      <h3>{{blog.title}}</h3>
      <p>作者：{{blog.author}}</p>
      <hr>
      <article>{{blog.content}}</article>
      <p>分类：</p>
      <ul>
        <li v-for="category in blog.categories" :key="category">
          {{category}}
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'add-blog',
  data () {
    return {
      blog:{
        title:"",
        content:"",
        categories:[],
        author:""
      },
      authors:["Mary","Yamy","Jammy"],
      submmited:false
    }
  },
  methods:{
     post:function(){
      // this.$http.post("https://myblog-fcfec.firebaseio.com/posts.json",this.blog)
      axios.post("/posts.json",this.blog)
        .then((data) => {
          console.log(data);
          this.submmited = true;
        });
     }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#add-blog *{
  box-sizing: border-box;
}

#add-blog{
  margin: 20px auto;
  max-width: 600px;
  padding: 20px;
}

label{
  display: block;
  margin: 20px 0 10px;
}

input[type="text"],textarea,select{
  width: 100%;
  display: block;
  padding: 8px;
}

textarea{
  height: 200px;
}

#checkboxes label{
  display: inline-block;
  margin-top: 0;
}

#checkboxes input{
  display: inline-block;
  margin-left: 10px; 
}

button{
  display: block;
  margin: 20px 0;
  background: crimson;
  color: #fff;
  border: 0;
  padding: 14px;
  border-radius: 4px;
  font-size: 18px;
  cursor: pointer;
}

#preview{
   padding: 10px 20px;
   border: 1px dotted #ccc;
   margin: 30px 0;
}

h3{
  margin-top: 10px; 
}

textarea,article{
  white-space: pre-wrap;
}
</style>











