<template>
	<div id="show-blogs">
		<h1>博客总览</h1>
		<input type="text" v-model="search" placeholder="搜索">
		<div v-for="blog in filteredBlogs" class="single-blog" :key="blog.key">
			<router-link v-bind:to="'/blog/' + blog.id">
			<h2 v-rainbow>{{blog.title | to-uppercase}}</h2>
			</router-link>
			<article>{{blog.content | snippet}}</article>
		</div>
	</div>
</template>


<script>
	
	// import HelloWorld from './components/HelloWorld'
	// import axios from 'axios'
	import axios from '../axios-auth.js'
	export default {
		name: 'show-blogs',
		data () {
			return {
				blogs:[],
				search:""
			}
		}, 
		// components: {}
		created(){
			// this.$http.get('https://myblog-fcfec.firebaseio.com/posts.json')
			axios.get('/posts.json')
			.then(function(data){
				// console.log(data.json());
				//得到的是对象，通过json处理并返回
				// return data.json();
				// this.blogs = data.body.slice(0,10);
				//axios不用json解析
				return data.data;
			})
			.then((data) => {
				//获取处理后的对象
				//将对象变为数组，否则不能用slice方法
				console.log(data);
				var blogsArray = [];
				for(let key in data){
					// console.log(key);
					data[key].id = key;
					blogsArray.push(data[key]);
				}
				// console.log(blogsArray);
				this.blogs = blogsArray;
			})
		},
		computed:{
			filteredBlogs:function(){
				//使用过滤器匹配标题搜索博客
				return this.blogs.filter((blog) =>{
					return blog.title.match(this.search);
				})
			}
		},
		filters:{
			//过滤器
			"to-uppercase":function(value){
				return value.toUpperCase();
			},
			snippet(value){
				return value.slice(0,100) + "...";
			}
		},
		directives:{
			"rainbow":{
				//自定义指令
					bind(el,binding,vnode){
						el.style.color = "#" + Math.random().toString(16).slice(2,8);
					}
			}
		}

	}
</script>


<style scoped>
#show-blogs{
	max-width: 800px;
	margin: 0 auto;
}
.single-blog{
	padding: 20px;
	margin: 20px 0;
	box-sizing: border-box;
	background: #eee;
	border: 1px dotted #aaa;
}

#show-blogs a{
	color: #444;
	text-decoration: none;
	/*cursor: pointer; */
}

input[type="text"]{
	padding: 8px;
	width: 100%;
	box-sizing: border-box;
}

</style>



